//列表提交
export function listCommit(props, data) {
    if (!data) {
        let select = checkSelected(props, false);
        if (!select.valid) {
            return;
        }
        if (select.valid) {
            let selectDatas = select.selectDatas;
            let pks = selectDatas && selectDatas.map(item => item.data.values[PRIMARY_KEY.head_id].value);
            let pkMapTs = new Map();
            selectDatas && selectDatas.map(item => {
                let pk = item.data.values[PRIMARY_KEY.head_id].value;
                let ts = item.data.values[FIELD.ts] && item.data.values[FIELD.ts].value;
                //主键与tsMap
                if (pk && ts) {
                    pkMapTs.set(pk, ts);
                }
            });
            data = { pks, pkMapTs};
        }
    }
    ajax({
        url: REQUEST_URL.commit,
        data: data,
        success: (res) => {
            if (res.success) { //成功
                if (res.data && res.data.workflow && (res.data.workflow == 'approveflow' || res.data.workflow == 'workflow')) {
                    this.setState({
                        compositeData: res.data,
                        compositeDisplay: true,
                        curPk: data.pks
                    });
                    PromptMessage.call(this, res);
                } else {
                    if(res.data.total && res.data.total > 1){//批量提交
                        PromptMessage.call(this, res);
                        commitRefresh(props);
                    }else{//单个提交
                        listRefresh(props);
                    }
                }
            } else { //失败
                PromptMessage.call(this, res);
                //toast({ color: STATUS.warning, content: props.json['M0Z20602-000015'] });/* 国际化处理： 提交失败*/
            }
        }
    });
}

//列表收回
export function listUnCommit(props, data) {
    if (!data) {
        let select = checkSelected(props, false);
        if (select.valid) {
            let selectDatas = select.selectDatas;
            let pks = selectDatas && selectDatas.map(item => item.data.values[PRIMARY_KEY.head_id].value);
            let pkMapTs = new Map();
            selectDatas && selectDatas.map(item => {
                let pk = item.data.values[PRIMARY_KEY.head_id].value;
                let ts = item.data.values[FIELD.ts] && item.data.values[FIELD.ts].value;
                //主键与tsMap
                if (pk && ts) {
                    pkMapTs.set(pk, ts);
                }
            });
            data = { pks, pkMapTs };
        }
    }
    ajax({
        url: REQUEST_URL.unCommit,
        data: data,
        success: (res) => {
            if (res.success) { //成功
                listRefresh(props);
            } else { //失败
                toast({ color: STATUS.warning, content: props.json['M0Z20602-000017'] });/* 国际化处理： 收回失败*/
            }
        },
        error:(res) => {
            commitRefresh(props);
            console.error(res);
            let msgContent = JSON.stringify(res.message);
            toast({ color: STATUS.danger, content: msgContent});
        }
    });
}


/**
 * 是否选中数据
 * 
 * @param {*} isCheckOne 是否选中一条数据
 * @returns 返回是否校验成功
 */
export function checkSelected(props, isCheckOne) {
    let valid = true;
    let selectDatas = props.table && props.table.getCheckedRows(LIST.table_id);
    if (isCheckOne && selectDatas.length > 1) {
        toast({
            color: STATUS.warning, content: props.json['M0Z20602-000012']/* 国际化处理： 请选中一行表体数据!*/
        });
        valid = false;
    } else if (selectDatas.length == 0) {
        toast({
            color: STATUS.warning, content: props.json['M0Z20602-000013']/* 国际化处理： 请选择表体数据操作!*/
        });
        valid = false;
    }
    return { valid, selectDatas };
}

