/**
 * 列表消息提示
 * @param {*} res           返回的response
 * @param {*} opername      操作名称
 */
export function PromptMessage(res) {
    let { status, msg } = res.data;
    let content;
    let total = res.data.total;
    let successNum = res.data.successNum;
    let failNum = res.data.failNum;
    content = '共' + '提交' + total + '条';/* 国际化处理： 共,条，*/
    content = content + ',成功' + successNum + '条';/* 国际化处理： 成功,条 ,,成功*/
    content = content + ',失败' + failNum + '条';/* 国际化处理： 失败,条,条*/
    let errMsgArr = res.data.errormessages;
    //添加详细信息
    let msgArr = "";
    if (res.data.data && res.data.data.length > 0) {
        let data = res.data.data;
        for (var i = 0; i < data.length; i++) {
            msgArr += (" \n 单据号=" + data[i].vbillno + ">>" + data[i].msg);
        }
    }
    //全部成功
    if (status == 0) {
        toast({
            color: "success",
            title: '提交' + msg,
            content: content + msgArr,
            TextArr: ['展开', '收起', '关闭'],/* 国际化处理： 展开,收起,关闭*/
            groupOperation: true
        });
    }
    //全部失败
    else if (status == 1) {
        toast({
            duration: "infinity",
            color: "danger",
            title: '提交' + msg,
            content: content + msgArr,
            TextArr: ['展开', '收起', '关闭'],/* 国际化处理： 展开,收起,关闭*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
    //部分成功
    else if (status == 2) {
        toast({
            duration: "infinity",
            color: "warning",
            title: '提交' + msg,
            content: content + msgArr,
            TextArr: ['展开', '收起', '关闭'],/* 国际化处理： 展开,收起,关闭*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
}

